export default {
	data() {
		return {
			reportName:'',
			imgList: [],
			uploadParams:null,
			imgsrc:'',
			type:'',//doctorReport就医报告   checkupReport体检报告
			deleteIndex:'',
			tipsValue:'请至少保留一张图片'
		};
	},
	onLoad(e) {
		console.log();
		this.type = e.type
		this.id = e.id?e.id:''
		this.init()
		// doctorReportAdd
		// checkupReportAdd
	},
	methods: {
		init(){
			const data = {
				purpose: 'useravatar'
			}
			this.$api.getUploadParams({
				data: data
			}).then(res => {
				console.log('getUploadParams')
				console.log(res)
				this.uploadParams = res.data
			}).catch(err=>{
				console.log(err);
			})
			if(this.id){
				switch (this.type){
					case 'doctorReport':
						this.findDoctorReport()
						break;
					case 'checkupReport':
						this.findCheckupReport()
						break;
				}
			}
		},
		addImage() {
			uni.authorize({
				scope: 'scope.camera',
				success:(res)=> {
					if (res.errMsg == 'authorize:ok') {
						console.log('选择上传');
						this.uploadReport();
					}
				},
				fail:(err)=> {
					uni.showToast({
						icon: 'none',
						title: "请先允许小程序获取相机访问权限"
					})
				}
			})
		},
		deletePic(url,index){
			this.imgsrc = url
			this.deleteIndex = index
			this.$refs['deletetip'].open()
		},
		confirmDelete(){
			this.imgList.splice(this.deleteIndex,1)
			this.$refs['deletetip'].close()
		},
		cancel(){
			this.$refs['deletetip'].close()
		},
		uploadReport() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'],
				sourceType: ['album','camera'],
			    success: (chooseImageRes) => {
			        const tempFilePaths = chooseImageRes.tempFilePaths;
					console.log(tempFilePaths);
					// 假设上传成功
					uni.showLoading({
						title:'上传中...'
					})
					uni.uploadFile({
						url: this.uploadParams.url,
						filePath: tempFilePaths[0],
						name: 'userAvatar',
						formData: {
							policy: this.uploadParams.policy,
							Signature: this.uploadParams.Signature
						},
						success:res=> {
							console.log(res);
							let data = JSON.parse(res.data)
							console.log(data.data);
							if (data.status === 'OK') {
								let url = data.data.location + data.data.filepath
								this.imgList.push({
									url:url
								})
								uni.hideLoading()
							} else {
								uni.showToast({
									title: res.message,
									icon: "none",
									duration: 2000
								})
							}
						},
						fail:(err)=> {
					        console.log(err); 
						},
					
					})
			    }
			});
		},
		submit(){
			if(this.reportName==''){
				this.tipsValue = '报告名称不能为空'
				this.$refs['showtip'].open();
				return
			}
			if(this.imgList<1){
				this.tipsValue = '请至少保留一张图片'
				this.$refs['showtip'].open();
				return
			}
			if(this.type=="doctorReport"){
				if(this.id!=''){
					// 修改 更新
					this.updateDoctorReport()
				}else{
					this.doctorReportAdd()
				}
			}else{
				if(this.id!=''){
					// 修改 更新
					this.updateCheckupReport()
				}else{
					this.checkupReportAdd()
				}
				
			}
			
		},
		confirm(){
			this.$refs['showtip'].close()
		},
		findDoctorReport(){
			this.$api.findDoctorReport({
				id:this.id
			}).then(res=>{
				if (res.status === 'OK') {
					this.imgList =JSON.parse(res.data.files)
					this.reportName = res.data.name
				} 
			}).catch(err=>{
				console.log(err);
			})
		},
		doctorReportAdd(){
			this.$api.doctorReportAdd({
				files: JSON.stringify(this.imgList), 
				name: this.reportName,
			}).then(res=>{
				console.log(res);
				if (res.status === 'OK') {
					uni.navigateBack({
						delta:1
					})
				}
			}).catch(err=>{
				console.log(err);
			})
		},
		updateDoctorReport(){
			this.$api.updateDoctorReport({
				files: JSON.stringify(this.imgList), 
				name: this.reportName,
				id:this.id
			}).then(res=>{
				console.log(res);
				if (res.status === 'OK') {
					uni.navigateBack({
						delta:1
					})
				} 
			}).catch(err=>{
				console.log(err);
			})
		},
		findCheckupReport(){
			this.$api.findCheckupReport({
				id:this.id
			}).then(res=>{
				if (res.status === 'OK') {
					this.imgList =JSON.parse(res.data.files)
					this.reportName = res.data.name
				} 
			}).catch(err=>{
				console.log(err);
			})
		},
		checkupReportAdd(){
			this.$api.checkupReportAdd({
				files: JSON.stringify(this.imgList), 
				name: this.reportName,
			}).then(res=>{
				console.log(res);
				if (res.status === 'OK') {
					uni.navigateBack({
						delta:1
					})
				} 
			}).catch(err=>{
				console.log(err);
			})
		},
		updateCheckupReport(){
			this.$api.updateCheckupReport({
				files: JSON.stringify(this.imgList), 
				name: this.reportName,
				id:this.id
			}).then(res=>{
				console.log(res);
				if (res.status === 'OK') {
					uni.navigateBack({
						delta:1
					})
				} 
			}).catch(err=>{
				console.log(err);
			})
		}

	},
}
